// src/app/components/decor/Garden.tsx

import React from "react";
import { GardenFlower } from "./GardenFlower";
import { GrassBlade } from "./GrassBlade";
import { VinesTop } from "./VinesTop";



function FlowerCluster({
  colors = ["#f43f5e", "#e11d48", "#fb7185"],
  flip = false,
}: {
  colors?: string[];
  flip?: boolean;
}) {
  const sizes = [22, 26, 30, 24];
  return (
    <div className={`flex items-end gap-2 ${flip ? "scale-x-[-1]" : ""}`}>
      <GrassBlade h={42} bend={8} />
      <GardenFlower size={sizes[0]} color={colors[0]} tilt={-3} />
      <GrassBlade h={28} bend={4} />
      <GardenFlower size={sizes[2]} color={colors[2]} tilt={2} />
      <GrassBlade h={36} bend={10} />
      <GardenFlower size={sizes[1]} color={colors[1]} tilt={-1} />
    </div>
  );
}

/* ------------------ Layout helpers ------------------ */

function MeadowRow({
  count = 8,
  flip = false,
  scale = 1,
  colors = ["#f43f5e", "#e11d48", "#fb7185"],
}: {
  count?: number;
  flip?: boolean;
  scale?: number;
  colors?: string[];
}) {
  // Evenly distribute clusters across a responsive row
  return (
    <div
      className="pointer-events-none w-full grid"
      style={{
        gridTemplateColumns: `repeat(${count}, minmax(0, 1fr))`,
        transform: `scale(${scale})`,
      }}
      aria-hidden
    >
      {Array.from({ length: count }).map((_, i) => (
        <div
          key={i}
          className={`garden-sway ${i % 2 ? "sway-fast" : "sway-slow"} flex items-end justify-center ${
            flip && i % 2 ? "scale-x-[-1]" : ""
          }`}
          style={{ animationDelay: `${(i % 6) * 0.3}s` }}
        >
          <FlowerCluster colors={colors} flip={flip && i % 2 === 0} />
        </div>
      ))}
    </div>
  );
}


/* ------------------ Main exported frames ------------------ */

export default function GardenFrame() {
  // Multi-layered decorative garden with broad coverage and varied sway speeds.
  return (
    <div className="pointer-events-none absolute inset-0 z-0" aria-hidden>
      {/* soft, taller ground gradient across bottom ~45% of screen */}
      <div
        className="absolute inset-x-0 bottom-0"
        style={{
          height: "45%",
          background:
            "linear-gradient(to top, rgba(22,163,74,0.14), rgba(22,163,74,0.04), rgba(22,163,74,0))",
        }}
      />

      {/* Background layer: large, lighter clusters spread tall for coverage */}
      <div className="absolute inset-x-0 bottom-0 h-[58%] opacity-40">
        <div className="mx-auto max-w-7xl px-2 space-y-6">
          <MeadowRow count={7} scale={1.05} colors={["#86efac", "#bbf7d0", "#a7f3d0"]} />
          <MeadowRow count={6} flip scale={1.1} colors={["#bbf7d0", "#a7f3d0", "#86efac"]} />
        </div>
      </div>

      {/* Midground layer */}
      <div className="absolute inset-x-0 bottom-0 h-[46%] opacity-60">
        <div className="mx-auto max-w-6xl px-3 space-y-4">
          <MeadowRow count={9} scale={1.0} colors={["#fda4af", "#fb7185", "#fca5a5"]} />
          <MeadowRow count={8} flip scale={1.0} colors={["#fb7185", "#f43f5e", "#e11d48"]} />
        </div>
      </div>

      {/* Foreground layer: densest at the base for a lush look */}
      <div className="absolute inset-x-0 bottom-0 h-[34%] opacity-85">
        <div className="mx-auto max-w-5xl px-4 space-y-3">
          <MeadowRow count={11} scale={0.98} colors={["#f43f5e", "#e11d48", "#fb7185"]} />
          <div className="flex items-end justify-between px-1">
            <div className="garden-sway sway-med">
              <FlowerCluster />
            </div>
            <div className="garden-sway sway-med">
              <FlowerCluster flip />
            </div>
          </div>
        </div>
      </div>

      {/* Corner anchors for ultra-wide */}
      <div className="garden-sway sway-med absolute left-2 bottom-1 hidden md:block">
        <FlowerCluster />
      </div>
      <div className="garden-sway sway-med absolute right-2 bottom-1 hidden md:block">
        <FlowerCluster flip />
      </div>

      {/* Top accents */}
      <VinesTop />

      <style jsx>{`
        .garden-sway {
          animation: garden-sway 5.6s ease-in-out infinite;
        }
        .sway-slow {
          animation-duration: 6.6s;
        }
        .sway-med {
          animation-duration: 5.2s;
        }
        .sway-fast {
          animation-duration: 4.2s;
        }

        @keyframes garden-sway {
          0%,
          100% {
            transform: translateY(0) rotate(-1.2deg);
          }
          50% {
            transform: translateY(-3px) rotate(1.2deg);
          }
        }

        .garden-drift {
          animation: garden-drift 20s linear infinite;
        }
        @keyframes garden-drift {
          0% {
            transform: translateX(0);
          }
          50% {
            transform: translateX(6px);
          }
          100% {
            transform: translateX(0);
          }
        }
      `}</style>
    </div>
  );
}
